body {
  background-color: #212424;
  color: #cfcfcf;
  font-family: 'Consolas', Arial, Helvetica, sans-serif;
  font-size: 14px;

  margin: 0;
  padding: 0;
}
.tab-list {
  width: 100%;
  height: auto;

  background-color: inherit;
}
button {
  width: auto;

  padding: 1em;

  background-color: inherit;
  border: none;
  outline: none;
  cursor: pointer;
  transition: 0.3s;

  color: #cfcfcf;
  font-family: 'Consolas', Arial, Helvetica, sans-serif;
}

button:hover {
  background-color: #353737;
}

button.active {
  background-color: #353737;
}
.tab-content {
  display: flex;
  flex-direction: column;
  flex-wrap: nowrap;
  justify-content: center;
  align-content: stretch;
  align-items: center;

  width: 100%;
  min-height: 70vh;

  background-color: #353737;
  border-radius: 0 0 2.5em 2.5em;
}
.tabbed-component {
  width: 100%;

  background-color: #212424;
}
article {
  position: relative;

  width: 100%;
  height: auto;
  
  overflow: hidden;
}

article img {
  position: absolute;
  z-index: 0;

  background-image: linear-gradient(#212424, #cfcfcf);

  width: 100%;
  height: auto;

  top: -50%;
  left: 0;

  transition: all 1s ease;
}

article a {
  text-decoration: none;
  width: 100%;
  height: auto;
  color: inherit;
}

article a:visited {
  text-decoration: none;
  color: inherit;
}

article a:hover + img {
  -webkit-transform: scale(1.2);
          transform: scale(1.2);
}

.content {
  position: relative;
  z-index: 1;

  width: 100%;
  min-height: 10em;

  padding: 1em;
  background-color: rgba(33,36,36, 0.85);
  transition: 0.3s;
}

.content:hover {
  background-color: rgba(33,36,36, 0.75);
}

.title {
  margin-top: 0.5em;
  font-size: 2em;
}

.description {
  margin-top: 1em;
  margin-bottom: 1em;
}

.loader {
  border: 1em solid #353737;
  border-top: 1em solid #212424;  
  border-bottom: 1em solid #212424; 
  border-radius: 50%;
  width: 6em;
  height: 6em;
  -webkit-animation: spin 2s linear infinite;
          animation: spin 2s linear infinite;
}

@-webkit-keyframes spin {
  0% { -webkit-transform: rotate(0deg); transform: rotate(0deg); }
  100% { -webkit-transform: rotate(360deg); transform: rotate(360deg); }
}

@keyframes spin {
  0% { -webkit-transform: rotate(0deg); transform: rotate(0deg); }
  100% { -webkit-transform: rotate(360deg); transform: rotate(360deg); }
}
.news-module {
  width: 70%;
  height: auto;

  flex: 1 0 auto;
}
.app {
  display: flex;
  flex-direction: column;
  flex-wrap: nowrap;
  justify-content: flex-start;
  align-content: stretch;
  align-items: center;
}

footer {
  text-align: center;
  flex-shrink: 0;
}

footer .attribution {
  margin-top: 2em;
  margin-bottom: 2em;
}

footer .attribution a {
  text-decoration: none;
  color: #cfcfcf;
}

footer .attribution a:hover {
  text-decoration: underline;
}

